import { Button, Stack } from "@mui/material";
import React from "react";

const Header = ({ user }) => {
  const styles = {
    stack: {
      height: "100%",
      alignItems: "center",
      paddingLeft: "10%",
      backgroundColor: "black"
    },
    button: {
      height: "80%"
    }
  };

  const goto = (location) => () => {
    window.top.location.assign(
      `http://${window.top.location.host}/${location}`
    );
  };

  const userExists = user !== null;

  return (
    <header>
      <Stack sx={styles.stack} direction="row" spacing={2}>
        <Button sx={styles.button} variant="contained" onClick={goto("home")}>
          Home
        </Button>
        {userExists ? (
          <Button
            sx={styles.button}
            variant="contained"
            onClick={goto("profile")}
          >
            Profile
          </Button>
        ) : (
          <Button
            sx={styles.button}
            variant="contained"
            onClick={goto("signin")}
          >
            Sign in
          </Button>
        )}
        {userExists ? (
          <Button
            sx={styles.button}
            variant="contained"
            onClick={goto("auth/logout")}
          >
            Log out
          </Button>
        ) : (
          <Button
            sx={styles.button}
            variant="contained"
            onClick={goto("signup")}
          >
            Sign up
          </Button>
        )}
      </Stack>
    </header>
  );
};

export default Header;
