import React from "react";
import { Buffer } from "buffer";

const MainFrame = ({ user }) => {
  const getFrameUrl = () => {
    const encodedUser = Buffer.from(user !== null ? JSON.stringify(user) : "")
      .toString("base64")
      .replaceAll("=", "");
    const query = new URLSearchParams(window.top.location.search);
    if (encodedUser !== "") query.append("user", encodedUser);
    return `${window.top.location.protocol}//${window.top.location.host}/proxy${
      window.top.location.pathname
    }?${query.toString()}`;
  };

  return (
    <main>
      <iframe title="main-frame" src={getFrameUrl()}></iframe>
    </main>
  );
};

export default MainFrame;
